import { useState } from 'react';
import './App.css';
import './logo.jpg'
import MainPage from './pages/main';
import HomeworkPage from './pages/homework';

function App() {
  const [pageName, setPageName] = useState('main')

  
  if (pageName === 'main') {
    return (<MainPage setPageName={setPageName}/>)
  } 
  else if (pageName === 'marks') {
    return (<div>marks</div>)
  }
  else if (pageName === 'homework') {
    return (<HomeworkPage setPageName={setPageName}/>)
  }

}

export default App;
